import A11yDialog from 'a11y-dialog';
import Choices from 'choices.js';
import 'choices.js/src/styles/choices';
import noUiSlider from 'nouislider';
import { fetchData } from '../../../site/scripts/_api';

/**
 * Product Sidebar Filter Manager
 * Handles URL synchronization, filter state management, and user interactions
 */
class ProductSidebarFilter {
  constructor() {
    // DOM elements
    this.sidebar = document.querySelector('.cmp-sidebar__filters');
    this.appliedFiltersContainer = document.querySelector('.cmp-sidebar__applied-filters');
    this.appliedFiltersList = document.querySelector('.applied-filters__list');
    this.clearAllBtn = document.querySelector('.applied-filters__clear');
    this.budgetSlider = document.getElementById('budget-range-slider');
    this.minBudgetInput = document.getElementById('minBudget');
    this.maxBudgetInput = document.getElementById('maxBudget');
    this.applyButton = document.querySelector('.btn-apply');

    // Constants
    this.DEFAULT_MIN = 500;
    this.DEFAULT_MAX = 5000;
    this.DEFAULT_BUDGET_RANGE = { min: 500, max: 5000 };

    // State management
    this.isInitializing = false;
    this.urlUpdateTimeout = null;
    this.pendingFilters = [];
    this.isMobile = window.innerWidth < 992; // Assuming 992px is the breakpoint for mobile/desktop

    // Initialize if sidebar exists
    if (this.sidebar) {
      this.init();
    }
  }

  /**
   * Get current URL parameters
   * @returns {Object} Object containing minBudget, maxBudget, and filters array
   */
  getUrlParams() {
    const params = new URLSearchParams(window.location.search);
    return {
      minBudget: parseInt(params.get('minBudget')) || this.DEFAULT_MIN,
      maxBudget: parseInt(params.get('maxBudget')) || this.DEFAULT_MAX,
      filters: params.getAll('filter').filter((f) => f.trim()), // Remove empty filters
    };
  }

  /**
   * Update URL parameters with debouncing
   * @param {Object} params - Parameters to update
   */
  updateUrlParams(params) {
    // Clear existing timeout to debounce URL updates
    if (this.urlUpdateTimeout) {
      clearTimeout(this.urlUpdateTimeout);
    }

    this.urlUpdateTimeout = setTimeout(() => {
      const url = new URL(window.location);
      const searchParams = new URLSearchParams();

      // Always add budget values to URL
      searchParams.set('minBudget', params.minBudget);
      searchParams.set('maxBudget', params.maxBudget);

      // Add filters
      params.filters.forEach((filter) => {
        if (filter.trim()) {
          searchParams.append('filter', filter);
        }
      });

      url.search = searchParams.toString();

      // Use pushState for better browser history management
      if (!this.isInitializing) {
        window.history.replaceState({ filters: params }, '', url);
      }
    }, 100); // 100ms debounce
  }

  /**
   * Get filter text from checkbox
   * @param {HTMLElement} checkbox - The checkbox element
   * @returns {string} Formatted filter text
   */
  getFilterText(checkbox) {
    const section = checkbox.closest('.cmp-accordion__item');
    if (!section) {
      return '';
    }

    const sectionTitle = section.querySelector('.cmp-accordion__title')?.textContent?.trim() || '';
    const optionText = checkbox.nextElementSibling?.textContent?.trim() || '';
    return `${sectionTitle}: ${optionText}`;
  }

  /**
   * Update applied filters container visibility
   */
  updateAppliedFiltersVisibility() {
    if (!this.appliedFiltersContainer || !this.appliedFiltersList) {
      return;
    }

    // Hide by default on both mobile and desktop
    this.appliedFiltersContainer.style.display = 'none';

    // Only show if there are active filters
    const hasFilters = this.appliedFiltersList.children.length > 0;
    if (hasFilters) {
      this.appliedFiltersContainer.style.display = 'block';
    }
  }

  /**
   * Add applied filter to the UI
   * @param {string} filterText - The filter text to add
   */
  addAppliedFilter(filterText) {
    if (!filterText.trim() || !this.appliedFiltersList) {
      return;
    }

    // Check if filter already exists
    const existing = [...this.appliedFiltersList.querySelectorAll('.applied-filter')].some(
      (el) => el.textContent.replace('×', '').trim() === filterText,
    );

    if (!existing) {
      const filterElement = document.createElement('span');
      filterElement.className = 'applied-filter';
      filterElement.setAttribute('role', 'listitem');

      // Extract filter category and value for better ARIA labeling
      const [category, value] = filterText.includes(':')
        ? filterText.split(':').map((s) => s.trim())
        : ['Filter', filterText.trim()];

      filterElement.innerHTML = `
        <span class="filter-text">${filterText}</span>
        <button class="remove-filter" aria-label="Remove ${category} filter: ${value}" type="button">×</button>
      `;
      this.appliedFiltersList.appendChild(filterElement);

      this.updateAppliedFiltersVisibility();
    }
  }

  /**
   * Remove applied filter from the UI
   * @param {string} filterText - The filter text to remove
   */
  removeAppliedFilter(filterText) {
    if (!this.appliedFiltersList) {
      return;
    }

    [...this.appliedFiltersList.querySelectorAll('.applied-filter')].forEach((el) => {
      const text =
        el.querySelector('.filter-text')?.textContent?.trim() ||
        el.textContent.replace('×', '').trim();
      if (text === filterText) {
        el.remove();
      }
    });

    this.updateAppliedFiltersVisibility();
  }

  /**
   * Update budget range in applied filters
   * @param {number} minVal - Minimum budget value
   * @param {number} maxVal - Maximum budget value
   */
  updateBudgetRangeFilter(minVal, maxVal) {
    const budgetFilterText = `Price Range: $${minVal} - $${maxVal}`;

    // Remove existing budget range filter
    this.removeBudgetRangeFilter();

    // Only add if not default values
    if (minVal !== this.DEFAULT_MIN || maxVal !== this.DEFAULT_MAX) {
      this.addAppliedFilter(budgetFilterText);
    }
  }

  /**
   * Remove budget range filter from applied filters
   */
  removeBudgetRangeFilter() {
    if (!this.appliedFiltersList) {
      return;
    }

    [...this.appliedFiltersList.querySelectorAll('.applied-filter')].forEach((el) => {
      const text = el.querySelector('.filter-text')?.textContent?.trim() || '';
      if (text.startsWith('Price Range:')) {
        el.remove();
      }
    });
  }

  /**
   * Track the last synced slider values to prevent duplicate updates
   */
  lastSyncedValues = { min: null, max: null };

  /**
   * Handle slider change
   * @param {boolean} isFinal - Whether this is the final change (user stopped sliding)
   */
  handleSliderChange(isFinal = false) {
    if (!this.budgetSlider || !this.budgetSlider.noUiSlider) return;

    const values = this.budgetSlider.noUiSlider.get();
    const [minVal, maxVal] = values.map(val => Math.round(parseFloat(val)));

    // Skip if values haven't changed
    if (this.lastSyncedValues.min === minVal && this.lastSyncedValues.max === maxVal) {
      return;
    }

    // Update display values
    const minDisplay = document.getElementById('budget-min-value');
    const maxDisplay = document.getElementById('budget-max-value');

    if (minDisplay) minDisplay.value = `$${minVal}`;
    if (maxDisplay) maxDisplay.value = `$${maxVal}`;

    // Update hidden inputs
    if (this.minBudgetInput) this.minBudgetInput.value = minVal;
    if (this.maxBudgetInput) this.maxBudgetInput.value = maxVal;

    // Skip updates during initialization
    if (this.isInitializing) return;

    // Update budget range in applied filters
    this.updateBudgetRangeFilter(minVal, maxVal);

    // For mobile, we'll wait for the apply button click
    // For desktop, sync immediately on final change only
    if (isFinal) {
      this.lastSyncedValues = { min: minVal, max: maxVal };
      this.syncStateWithUrl();
    }
  }

  /**
   * Show loading state for filters
   */
  showLoadingState() {
    // Add loading class to the product grid or a loading overlay
    const productGrid = document.getElementById('product-grid');
    if (productGrid) {
      productGrid.classList.add('loading');
    }
  }

  /**
   * Hide loading state for filters
   */
  hideLoadingState() {
    const productGrid = document.getElementById('product-grid');
    if (productGrid) {
      productGrid.classList.remove('loading');
    }
  }

  /**
   * Initialize budget slider with noUiSlider
   */
  initBudgetSlider() {
    if (!this.budgetSlider) {
      return;
    }

    // Destroy existing slider if it exists
    if (this.budgetSlider.noUiSlider) {
      this.budgetSlider.noUiSlider.destroy();
    }

    const { dataset } = this.budgetSlider;
    const min = parseInt(dataset.min || this.DEFAULT_BUDGET_RANGE.min, 10);
    const max = parseInt(dataset.max || this.DEFAULT_BUDGET_RANGE.max, 10);
    const start = dataset.start ? JSON.parse(dataset.start) : [min, max];
    const step = dataset.step ? parseInt(dataset.step, 10) : 1;

    noUiSlider.create(this.budgetSlider, {
      start,
      connect: true,
      step: step,
      margin: step,
      range: { min, max },
      format: {
        to: (value) => Math.round(value),
        from: (value) => Number(value),
      },
    });

    // Handle slider changes
    this.budgetSlider.noUiSlider.on('update', (values, handle) => {
      this.handleSliderChange(false);
    });

    // Handle slider change end (user stopped sliding)
    this.budgetSlider.noUiSlider.on('change', () => {
      this.handleSliderChange(true);
    });
  }

  /**
   * Get currently selected filters
   * @returns {Array} Array of filter text strings
   */
  getCurrentFilters() {
    const filters = [];

    // Add price range filter if not default
    if (this.minBudgetInput && this.maxBudgetInput) {
      const minBudget = parseInt(this.minBudgetInput.value) || this.DEFAULT_MIN;
      const maxBudget = parseInt(this.maxBudgetInput.value) || this.DEFAULT_MAX;

      if (minBudget !== this.DEFAULT_MIN || maxBudget !== this.DEFAULT_MAX) {
        filters.push(`$${minBudget}-$${maxBudget}`);
      }
    }

    // Add checkbox filters (only the value part after colon)
    const checkboxFilters = Array.from(this.sidebar.querySelectorAll('input[type="checkbox"]:checked'))
      .map((checkbox) => {
        const text = this.getFilterText(checkbox);
        return text.split(':').pop().trim(); // Get just the value part after the colon
      })
      .filter((text) => text.trim());

    return [...filters, ...checkboxFilters];
  }

  /**
   * Synchronize current state with URL
   */
  syncStateWithUrl() {
    if (this.isInitializing) {
      return;
    }

    const currentFilters = this.getCurrentFilters();
    const currentBudget = {
      minBudget: parseInt(this.minBudgetInput?.value) || this.DEFAULT_MIN,
      maxBudget: parseInt(this.maxBudgetInput?.value) || this.DEFAULT_MAX,
    };

    this.updateUrlParams({
      ...currentBudget,
      filters: currentFilters,
    });

    // Clear the product grid immediately when filters change
    const productGrid = document.getElementById('product-grid');
    if (productGrid) {
      productGrid.innerHTML = '';
    }

    // Dispatch product-filter-applied event to notify ProductListingManager of filter changes
    document.dispatchEvent(
      new CustomEvent('product-filter-applied', { 
        detail: { 
          filters: currentFilters,
          resetPage: true
        } 
      }),
    );
  }

  /**
   * Handle checkbox change event
   * @param {HTMLElement} checkbox - The checkbox element that changed
   * @param {boolean} forceApply - Whether to force apply the filter (bypass mobile/desktop logic)
   */
  handleCheckboxChange(checkbox, forceApply = false) {
    const filterText = this.getFilterText(checkbox);
    if (!filterText) {
      return;
    }

    if (checkbox.checked) {
      this.addAppliedFilter(filterText);
    } else {
      this.removeAppliedFilter(filterText);
    }

    // For desktop or when force applying, sync immediately
    // For mobile, we'll wait for the apply button click
    if (!this.isMobile || forceApply) {
      this.syncStateWithUrl();
    } else {
      // Store the filter for later application
      const filterIndex = this.pendingFilters.findIndex((f) => f.id === checkbox.id);
      if (checkbox.checked) {
        if (filterIndex === -1) {
          this.pendingFilters.push({
            id: checkbox.id,
            text: filterText,
            checked: true,
          });
        }
      } else {
        if (filterIndex !== -1) {
          this.pendingFilters.splice(filterIndex, 1);
        }
      }
    }
  }

  /**
   * Apply pending filters (for mobile)
   */
  applyPendingFilters() {
    this.pendingFilters = [];
    this.syncStateWithUrl();

    // Close the dialog using a11y-dialog's close button
    const closeButton = document.querySelector('[data-a11y-dialog-hide="product-filter-dialog"]');
    if (closeButton) {
      closeButton.click();
    }
  }

  /**
   * Cancel pending filters (for mobile)
   */
  cancelPendingFilters() {
    // Revert checkboxes to their previous state
    this.pendingFilters.forEach((filter) => {
      const checkbox = document.getElementById(filter.id);
      if (checkbox) {
        checkbox.checked = filter.checked;
      }
    });
    this.pendingFilters = [];
  }

  /**
   * Setup checkbox change event handlers
   */
  setupCheckboxHandlers() {
    this.sidebar.querySelectorAll('input[type="checkbox"]').forEach((checkbox) => {
      checkbox.addEventListener('change', () => {
        const filterText = this.getFilterText(checkbox);
        if (!filterText) {
          return;
        }

        if (checkbox.checked) {
          this.addAppliedFilter(filterText);
        } else {
          this.removeAppliedFilter(filterText);
        }

        // For desktop or when force applying, sync immediately
        // For mobile, we'll wait for the apply button click
        if (!this.isMobile) {
          this.syncStateWithUrl();
        } else {
          // Store the filter for later application
          const filterIndex = this.pendingFilters.findIndex((f) => f.id === checkbox.id);
          if (checkbox.checked) {
            if (filterIndex === -1) {
              this.pendingFilters.push({
                id: checkbox.id,
                text: filterText,
                checked: true,
              });
            }
          } else if (filterIndex !== -1) {
            this.pendingFilters.splice(filterIndex, 1);
          }
        }
      });
    });
  }

  /**
   * Setup remove filter button handlers
   */
  setupRemoveFilterHandlers() {
    // Use event delegation for better performance
    document.addEventListener('click', (e) => {
      if (!e.target.closest('.remove-filter')) {
        return;
      }

      e.preventDefault();
      const filterElement = e.target.closest('.applied-filter');
      if (!filterElement) {
        return;
      }

      const filterText =
        filterElement.querySelector('.filter-text')?.textContent?.trim() ||
        filterElement.textContent.replace('×', '').trim();

      // Find and uncheck the corresponding checkbox
      this.sidebar.querySelectorAll('input[type="checkbox"]').forEach((checkbox) => {
        if (this.getFilterText(checkbox) === filterText) {
          checkbox.checked = false;
        }
      });

      this.removeAppliedFilter(filterText);
      this.syncStateWithUrl();
    });
  }

  /**
   * Setup clear all filters handler
   */
  setupClearAllHandler() {
    if (!this.clearAllBtn) {
      return;
    }

    this.clearAllBtn.addEventListener('click', (e) => {
      e.preventDefault();

      // Clear all checkboxes
      this.sidebar.querySelectorAll('input[type="checkbox"]').forEach((checkbox) => {
        checkbox.checked = false;
      });

      // Reset budget slider
      if (this.budgetSlider?.noUiSlider) {
        this.budgetSlider.noUiSlider.set([this.DEFAULT_MIN, this.DEFAULT_MAX]);
      }

      // Clear applied filters
      if (this.appliedFiltersList) {
        this.appliedFiltersList.innerHTML = '';
      }

      this.updateAppliedFiltersVisibility();

      // Reset budget range filter
      this.updateBudgetRangeFilter(this.DEFAULT_MIN, this.DEFAULT_MAX);

      // Update URL
      this.syncStateWithUrl();
    });
  }

  /**
   * Initialize filters from URL parameters
   */
  async initializeFromUrl() {
    this.isInitializing = true;

    try {
      const { minBudget, maxBudget, filters } = this.getUrlParams();

      // Set budget from URL
      if (this.budgetSlider?.noUiSlider) {
        this.budgetSlider.noUiSlider.set([minBudget, maxBudget]);
        // Add budget range to applied filters if not default values
        if (minBudget !== this.DEFAULT_MIN || maxBudget !== this.DEFAULT_MAX) {
          this.updateBudgetRangeFilter(minBudget, maxBudget);
        }
      }

      // Set checkboxes from URL filters
      if (filters.length > 0) {
        // Expand all accordions that contain filters
        const accordionsToExpand = new Set();

        filters.forEach((filterText) => {
          const [sectionTitle] = filterText.split(':').map((s) => s.trim());
          accordionsToExpand.add(sectionTitle);
        });

        // Wait for accordions to expand, then apply filters
        await new Promise((resolve) => setTimeout(resolve, 150));

        filters.forEach((filterText) => {
          this.applyFilterFromText(filterText);
          this.addAppliedFilter(filterText);
        });
      }
    } catch (error) {
      console.error('Error initializing from URL:', error);
    } finally {
      this.isInitializing = false;
    }
  }

  /**
   * Apply a filter based on filter text
   * @param {string} filterText - The filter text to apply
   */
  applyFilterFromText(filterText) {
    if (!filterText.includes(':')) {
      return;
    }

    const [sectionTitle, optionText] = filterText.split(':').map((s) => s.trim());

    this.sidebar.querySelectorAll('.cmp-accordion__item').forEach((item) => {
      const title = item.querySelector('.cmp-accordion__title')?.textContent?.trim();
      if (title === sectionTitle) {
        item.querySelectorAll('input[type="checkbox"]').forEach((checkbox) => {
          const label = checkbox.nextElementSibling?.textContent?.trim();
          if (label === optionText && !checkbox.checked) {
            checkbox.checked = true;
            this.addAppliedFilter(filterText);
          }
        });
      }
    });
  }

  /**
   * Handle browser back/forward navigation
   */
  setupPopstateHandler() {
    window.addEventListener('popstate', () => {
      // Re-initialize from URL when user navigates back/forward
      this.initializeFromUrl();
    });
  }

  /**
   * Setup mobile filter buttons
   */
  setupMobileButtons() {
    if (this.applyButton) {
      this.applyButton.addEventListener('click', () => {
        // Apply all pending filters
        this.applyPendingFilters();
      });
    }
  }

  /**
   * Close the mobile filter dialog
   */
  closeMobileFilterDialog() {
    const closeButton = document.querySelector('[data-a11y-dialog-hide="product-filter-dialog"]');
    if (closeButton) {
      closeButton.click();
    }
  }

  /**
   * Handle window resize to update mobile/desktop state
   */
  handleResize() {
    const wasMobile = this.isMobile;
    this.isMobile = window.innerWidth < 992; // Match your mobile breakpoint

    // If switching from mobile to desktop, apply any pending filters
    if (wasMobile && !this.isMobile && this.pendingFilters.length > 0) {
      this.applyPendingFilters();
    }
  }

  /**
   * Initialize all components and event handlers
   */
  init() {
    // Hide applied filters by default
    if (this.appliedFiltersContainer) {
      this.appliedFiltersContainer.style.display = 'none';
    }

    this.initBudgetSlider();
    this.setupCheckboxHandlers();
    this.setupRemoveFilterHandlers();
    this.setupClearAllHandler();
    this.setupMobileButtons();
    this.initializeFromUrl();
    this.setupPopstateHandler();

    // Add resize handler for mobile/desktop detection
    window.addEventListener('resize', this.handleResize.bind(this));
  }
}

/**
 * Product Listing Manager
 * Handles dynamic product loading from JSON, pagination, and sorting
 */
class ProductListingManager {
  constructor() {
    this.productGrid = document.getElementById('product-grid');
    this.productCountElement = document.getElementById('product-count');
    this.showMoreBtn = document.getElementById('show-more-products');
    this.productsPerPage = 4;
    this.currentPage = 1;
    this.allProducts = [];
    this.totalProducts = 0;
    this.hasMore = true;
    this.currentSort = 'newest';
    this.filters = [];

    this.init();
  }

  async init() {
    this.bindEvents();
    await this.fetchProducts();
  }

  bindEvents() {
    this.showMoreBtn?.addEventListener('click', () => this.loadMoreProducts());

    // Listen for filter changes
    document.addEventListener('product-filter-applied', (event) => {
      if (event.detail.resetPage) {
        this.currentPage = 1;
        this.allProducts = [];
      }
      this.filters = event.detail.filters;
      this.fetchProducts();
    });

    // Listen for sort changes
    document.addEventListener('product-sort-applied', (event) => {
      this.currentPage = 1;
      this.allProducts = [];
      this.currentSort = event.detail.sort;
      this.fetchProducts();
    });
  }

  async fetchProducts() {
    try {
      if (this.currentPage === 1) {
        this.productGrid.innerHTML = '';
      }

      // Show loading state
      this.showMoreBtn.innerHTML = 'Loading...';

      // Convert filters array to a JSON string and encode it for URL
      const filtersParam = encodeURIComponent(JSON.stringify(this.filters));
      const response = await fetchData(
        `products-paginated.json?page=${this.currentPage}&sort=${this.currentSort}&filters=${filtersParam}`,
      );
      this.allProducts = [...this.allProducts, ...response.products];
      this.totalProducts = response.totalProducts;
      this.hasMore = response.hasMore || false;

      // Update product count
      this.updateProductCount();
      this.renderProducts(response.products);

      this.showMoreBtn.style.display = this.hasMore ? 'block' : 'none';
    } catch (error) {
      console.error('Error loading products:', error);
      this.showError('Failed to load products. Please try again.');
      throw error;
    } finally {
      // Reset loading state
      this.showMoreBtn.innerHTML = 'Show more <span class="icon icon--arrow-bottom"></span>';
    }
  }

  updateProductCount() {
    if (this.productCountElement) {
      this.productCountElement.textContent = `${this.totalProducts} Products Found`;
    }
  }

  renderProducts(products) {
    if (!this.productGrid) {
      console.error('Product grid element not found');
      return;
    }

    try {
      // Render products
      products.forEach((product) => {
        try {
          const cardHtml = window.renderProductCard(product);
          const productCard = document.createElement('div');
          productCard.className =
            'layout-grid__col layout-grid__col--span-6 layout-grid__col--sm-span-12';
          productCard.innerHTML = cardHtml;
          this.productGrid.appendChild(productCard);
        } catch (error) {
          console.error('Error rendering product card:', error, product);
        }
      });
    } catch (error) {
      console.error('Error in renderProducts:', error);
    }
  }

  loadMoreProducts() {
    if (!this.hasMore) return;

    this.currentPage++;
    this.fetchProducts();
  }

  showError(message) {
    if (this.productGrid) {
      this.productGrid.innerHTML = `
        <div class="cmp-product-listing__error">
          <p>${message}</p>
        </div>
      `;
    }
  }
}

/**
 * Sort Dropdown Manager
 * Handles ARIA states and accessibility for the sort dropdown
 */
class SortDropdownManager {
  constructor(selectElement) {
    this.selectElement = selectElement;
    this.choicesInstance = null;
    this.init();
  }

  init() {
    if (!this.selectElement) return;

    // Initialize Choices.js with accessibility enhancements
    this.choicesInstance = new Choices(this.selectElement, {
      searchEnabled: false,
      itemSelectText: '',
      shouldSort: false,
      callbackOnInit: () => {
        this.setupAriaAttributes();
      },
    });

    // Setup event listeners for ARIA state management
    this.setupEventListeners();
  }

  setupAriaAttributes() {
    const choicesContainer = this.selectElement.closest('.choices');
    const choicesInner = choicesContainer?.querySelector('.choices__inner');

    if (choicesContainer && choicesInner) {
      // Set initial ARIA attributes
      choicesInner.setAttribute('aria-label', 'Sort products by');
      choicesInner.setAttribute('aria-expanded', 'false');
      choicesInner.setAttribute('role', 'combobox');

      // Update options with proper ARIA attributes
      this.updateOptionAriaAttributes();
    }
  }

  updateOptionAriaAttributes() {
    const selectedValue = this.selectElement.value;
    const options = this.selectElement.querySelectorAll('option');

    options.forEach((option) => {
      const isSelected = option.value === selectedValue;
      option.setAttribute('aria-selected', isSelected.toString());
      option.setAttribute('role', 'option');
    });
  }

  setupEventListeners() {
    const choicesContainer = this.selectElement.closest('.choices');
    const choicesInner = choicesContainer?.querySelector('.choices__inner');

    if (!choicesContainer || !choicesInner) return;

    // Handle dropdown open/close states
    this.selectElement.addEventListener('showDropdown', () => {
      choicesInner.setAttribute('aria-expanded', 'true');
    });

    this.selectElement.addEventListener('hideDropdown', () => {
      choicesInner.setAttribute('aria-expanded', 'false');
    });

    // Handle selection changes
    this.selectElement.addEventListener('change', () => {
      this.updateOptionAriaAttributes();

      // Announce selection to screen readers
      const selectedOption = this.selectElement.options[this.selectElement.selectedIndex];
      if (selectedOption) {
        this.announceSelection(selectedOption.textContent);
      }

      // Dispatch product-sort-applied event to notify ProductListingManager of sort changes
      document.dispatchEvent(
        new CustomEvent('product-sort-applied', { detail: { sort: selectedOption.value } }),
      );
    });
  }

  announceSelection(selectedText) {
    // Create a live region announcement for screen readers
    const announcement = document.createElement('div');
    announcement.setAttribute('aria-live', 'polite');
    announcement.setAttribute('aria-atomic', 'true');
    announcement.className = 'sr-only';
    announcement.textContent = `Sort order changed to ${selectedText}`;

    document.body.appendChild(announcement);

    // Remove the announcement after it's been read
    setTimeout(() => {
      document.body.removeChild(announcement);
    }, 1000);
  }
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
  const sortElement = document.querySelector('#sort-by');
  if (sortElement) {
    new SortDropdownManager(sortElement);
  }

  new ProductSidebarFilter();
  new ProductListingManager();

  // Handle show filter dialog button
  const filterTrigger = document.querySelector('[data-a11y-dialog-show="product-filter-dialog"]');
  const filterDialog = document.getElementById('product-filter-dialog');

  if (filterTrigger && filterDialog) {
    const dialog = new A11yDialog(filterDialog);

    // Show dialog when filter trigger is clicked
    filterTrigger.addEventListener('click', () => {
      filterDialog.classList.add('dialog-container');
      dialog.show();
    });

    // Remove dialog attributes when window is resized above 1024px
    window.addEventListener('resize', () => {
      if (!matchMedia('(max-width: 1024px)').matches) {
        filterDialog.classList.remove('dialog-container');
        filterDialog.removeAttribute('aria-hidden');
        filterDialog.removeAttribute('aria-modal');
        filterDialog.removeAttribute('role');
      }
    });
  }
});
