class ProductCompare {
  constructor(container) {
    this.container = container;
    if (!this.container) {
      return;
    }

    this.COMPARE_LIMIT = 4;
    this.COMPARE_KEY = 'compare-products';

    this.state = {
      products: [],
      isCollapsed: true,
    };

    this.dom = {
      list: this.container.querySelector('.cmp-product-compare__list'),
      countSpan: this.container.querySelector('[data-count]'),
      errorContainer: this.container.querySelector('.cmp-product-compare__error-container'),
      compareBtn: this.container.querySelector("[data-action='compare']"),
      toggleBtn: this.container.querySelector('.cmp-product-compare__toggle'),
    };

    this.init();
  }

  init() {
    this.updateState({ initialize: true });
    this.attachEventListeners();
    this.setupMutationObserver();
  }

  // --- State Management ---
  updateState({ initialize = false } = {}) {
    this.state.products = this.getStoredProducts();
    this.state.isCollapsed = initialize ?? false;
    this.render();
    this.updateAllCheckboxes();
  }

  getStoredProducts() {
    try {
      return JSON.parse(localStorage.getItem(this.COMPARE_KEY)) || [];
    } catch (e) {
      console.error('Error reading compare products:', e);
      return [];
    }
  }

  storeProducts(products) {
    try {
      localStorage.setItem(this.COMPARE_KEY, JSON.stringify(products));
      this.updateState(); // Update own state and re-render
    } catch (e) {
      console.error('Error storing compare products:', e);
    }
  }

  // --- Event Handling ---
  attachEventListeners() {
    // Listen for actions within the component itself (e.g., remove, clear all)
    this.container.addEventListener('click', this.handleContainerClick.bind(this));

    // Listen for changes on any 'add to compare' checkbox on the page
    document.addEventListener('change', this.handleCheckboxChange.bind(this));
  }

  handleContainerClick(event) {
    const actionTarget = event.target.closest('[data-action]');
    const removeTarget = event.target.closest('.cmp-product-compare__remove');

    if (removeTarget) {
      event.preventDefault();
      this.removeProduct(removeTarget.dataset.id);
      return;
    }

    if (!actionTarget) {
      return;
    }
    event.preventDefault();
    const action = actionTarget.dataset.action;

    switch (action) {
      case 'clear-all':
        this.clearAll();
        break;
      case 'toggle-collapse':
        this.toggleCollapse();
        break;
      case 'compare':
        if (this.state.products.length < 2) {
          this.showError('minimum');
        }
        break;
    }
  }

  // --- Actions ---
  handleCheckboxChange(event) {
    const checkbox = event.target;
    if (!checkbox.matches('[data-add-to-compare]')) {
      return;
    }

    const product = {
      id: checkbox.dataset.id,
      name: checkbox.dataset.name,
      sku: checkbox.dataset.sku,
      pdpUrl: checkbox.dataset.pdp,
      image: checkbox.dataset.image,
    };

    if (checkbox.checked) {
      if (this.state.products.length >= this.COMPARE_LIMIT) {
        alert(`You can only compare up to ${this.COMPARE_LIMIT} products.`);
        checkbox.checked = false;
        return;
      }
      if (!this.state.products.some((p) => p.id === product.id)) {
        const updatedProducts = [...this.state.products, product];
        this.storeProducts(updatedProducts);
      }
    } else {
      this.removeProduct(product.id);
    }
  }

  removeProduct(idToRemove) {
    const updatedProducts = this.state.products.filter((p) => p.id !== idToRemove);
    this.storeProducts(updatedProducts);
  }

  clearAll() {
    this.storeProducts([]);
  }

  toggleCollapse(forceCollapse = null) {
    this.state.isCollapsed = forceCollapse ?? !this.state.isCollapsed;
    this.render();
  }

  // --- Rendering ---
  showError(type) {
    this.dom.errorContainer.querySelectorAll('[data-error]').forEach((el) => (el.hidden = true));
    const errorEl = this.dom.errorContainer.querySelector(`[data-error="${type}"]`);
    if (errorEl) {
      this.dom.errorContainer.classList.add('has-error');
      errorEl.hidden = false;
    }
  }

  createSlotHTML(product) {
    if (product) {
      return `
        <div class="cmp-product-compare__item">
          <a href="${product.pdpUrl}" class="cmp-product-compare__link">
            <div class="cmp-product-compare__image-block">
              <img src="${product.image}" alt="${product.name}" class="cmp-product-compare__image" />
            </div>
            <div class="cmp-product-compare__text-block">
              <div class="cmp-product-compare__name">${product.name}</div>
              <div class="cmp-product-compare__sku">${product.sku}</div>
            </div>
          </a>
          <button class="cmp-product-compare__remove" data-id="${product.id}" aria-label="Remove product"></button>
        </div>
      `;
    } else {
      return `
        <div class="cmp-product-compare__placeholder">
          <span class="cmp-product-compare__add-icon" aria-hidden="true"></span>
          <span>Add up to 4 products to compare</span>
        </div>
      `;
    }
  }

  updateAllCheckboxes() {
    const productIds = new Set(this.state.products.map((p) => p.id));
    document.querySelectorAll('[data-add-to-compare]').forEach((checkbox) => {
      checkbox.checked = productIds.has(checkbox.dataset.id);
    });
  }

  setupMutationObserver() {
    const observer = new MutationObserver((mutationsList) => {
      for (const mutation of mutationsList) {
        if (mutation.type === 'childList') {
          mutation.addedNodes.forEach((node) => {
            if (node.nodeType === Node.ELEMENT_NODE) {
              const checkboxes = node.querySelectorAll('[data-add-to-compare]');
              if (checkboxes.length > 0) {
                this.updateAllCheckboxes();
              }
            }
          });
        }
      }
    });
    observer.observe(document.body, { childList: true, subtree: true });
  }

  render() {
    const productCount = this.state.products.length;

    this.container.classList.toggle('is-hidden', productCount === 0);
    this.container.classList.toggle('is-collapsed', this.state.isCollapsed);
    if (this.dom.toggleBtn) {
      this.dom.toggleBtn.setAttribute('aria-expanded', !this.state.isCollapsed);
    }

    this.dom.countSpan.textContent = productCount;

    this.dom.compareBtn.classList.toggle('has-products', productCount >= 2);

    this.dom.errorContainer.classList.remove('has-error');
    this.dom.errorContainer.querySelectorAll('[data-error]').forEach((el) => (el.hidden = true));

    this.dom.list.innerHTML = '';
    for (let i = 0; i < this.COMPARE_LIMIT; i++) {
      const product = this.state.products[i];
      const li = document.createElement('li');
      li.className = `cmp-product-compare__slot ${product ? 'is-filled' : 'is-empty'}`;
      li.innerHTML = this.createSlotHTML(product);
      this.dom.list.appendChild(li);
    }
  }
}

document.addEventListener('DOMContentLoaded', () => {
  const compareContainer = document.querySelector('[data-compare-container]');
  if (compareContainer) {
    new ProductCompare(compareContainer);
  }
});
